<template>
  <div class="addAddress mescroll">
    <div class="list">
      <div class="item flex">
        <div class="label">收货人</div>
        <div class="value flex-2">
          <div>
            <van-field v-model="name" placeholder="请输入收货人姓名" clearable />
          </div>
          <div class="prompt">
            <span>*</span>国务院《快递暂行条约》要求快递公司接到真实姓名方可发货，请填写您的真实姓名，敬请谅解！
          </div>
        </div>
      </div>
      <div class="item item-center flex">
        <div class="label">联系方式</div>
        <div class="value flex-2">
          <van-field v-model="telno" placeholder="请输入联系方式" type="tel" clearable />
        </div>
      </div>
      <div class="item item-center flex area">
        <div class="label">所在地区</div>
        <div class="value flex-2" :style="areaName?'':'color:#999'" @click="showAddress = true">{{areaName?areaName:'请选择所在地区'}}</div>
        <div class="arrow">
          <img src="../../assets/img/icon/left.png" alt />
        </div>
      </div>
      <div class="item flex">
        <div class="label">详细地址</div>
        <div class="value flex-2">
          <van-field autosize :rows="2" v-model="addr" placeholder="请输入详细地址" type="textarea" clearable />
        </div>
      </div>
      <div class="item item-bottom flex">
        <div class="label">设为默认地址</div>
        <div class="value flex-2">
          <van-switch v-model="checked" active-color="#eb5252" bind:change="onChange" />
        </div>
      </div>
    </div>
    <div class="btnWrap">
      <div class="btn" @click="save">保存</div>
    </div>
    <van-popup v-model="showAddress" position="bottom">
      <van-area :area-list="areaList" :value="countyId" @confirm="areaConfirm" @cancel="showAddress = false" />
    </van-popup>
  </div>
</template>
<script>
import { Switch, Field, Popup, Area } from "vant";
import { addAddress, getAddressDetail, editAddress } from "@/api";
import areaList from "@/utils/AreaList";
export default {
  components: {
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Popup.name]: Popup,
    [Area.name]: Area
  },
  data() {
    return {
      id: "",
      name: "",
      telno: "",
      checked: false,
      areaName: "",
      addr: "",
      areaList: areaList,
      showAddress: false,
      provinceId: "",
      cityId: "",
      countyId: ""
    };
  },
  methods: {
    areaConfirm(obj) {
      console.log(obj);
      this.showAddress = false;
      this.areaName = `${obj[0].name} ${obj[1].name} ${obj[2].name}`;
      this.provinceId = obj[0].code;
      this.cityId = obj[1].code;
      this.countyId = obj[2].code;
    },
    save() {
      if (!this.name) {
        this.$toast("请输入姓名");
        return;
      }
      if (!this.telno) {
        this.$toast("请输入联系方式");
        return;
      }
      if (!this.areaName) {
        this.$toast("请选择所在区域");
        return;
      }
      if (!this.addr) {
        this.$toast("请输入详细地址");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      addAddress({
        addrCode: this.id || undefined,
        userName: this.name,
        userMobile: this.telno,
        province_id: this.provinceId,
        city_id: this.cityId,
        areaCode: this.countyId,
        addrAddr: this.addr,
        isDefault: this.checked ? 1 : 0,
        addrDes: this.areaName.replace(/\s+/g, "")
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast(this.id ? "修改成功" : "新增成功");
          setTimeout(() => {
            this.$router.back();
          });
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getAddressDetail({ id: this.$route.query.id }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.id = res.data.addrCode;
          this.name = res.data.userName2;
          this.telno = res.data.userMobile;
          this.checked = res.data.isDefault == 1 ? true : false;
          this.areaName = res.data.areaNames.replace(/\//g, " ");
          this.addr = res.data.addrAddr;
          this.provinceId = res.data.areaCode.substring(0, 2) + "0000";
          this.cityId = res.data.areaCode.substring(0, 4) + "00";
          this.countyId = res.data.areaCode;
        } else {
          this.$toast(res.msg);
        }
      });
    }
  },
  mounted() {
    document.title = this.$route.query.id ? "修改收货地址" : "新增收货地址";
    if (this.$route.query.id) {
      this.loadData();
    }
  }
};
</script>
<style lang="scss" scoped>
.addAddress {
  background: #fff;
  .list {
    padding: 0 15px;
    .flex {
      display: flex;
    }
    .item {
      padding: 15px 0;
      border-bottom: 1pt solid #efefef;
      .flex-2 {
        flex: 2;
      }

      .label {
        font-size: 15px;
        line-height: 30px;
        font-weight: bold;
        margin-right: 15px;
        width: 60px;
      }
      .value {
        input {
          height: 30px;
          font-size: 15px;
          color: #666;
          width: 100%;
        }
        textarea {
          width: 100%;
          height: 30px;
          font-size: 15px;
          color: #666;
          margin-top: 7px;
        }
      }

      .value {
        .prompt {
          color: #999;
          font-size: 12px;
          line-height: 20px;
          span {
            color: $base-color;
          }
        }
      }
    }
    .area {
      .value {
        font-size: 15px;
        color: #666;
      }
      img {
        width: 5px;
      }
    }
    .item-center {
      align-items: center;
    }
    .item-bottom {
      .label {
        width: 100px;
        line-height: 37px;
      }
      .value {
        text-align: right;
        margin: 3.5px 0 0;
      }
    }
  }
  .btnWrap {
    background: #fff;
    margin: 20px 0 0;
    width: calc(100% - 30px);
    padding: 0 15px 10px;
    .btn {
      background: $base-color;
      color: #fff;
      font-size: 18px;
      width: 100%;
      text-align: center;
      height: 50px;
      line-height: 50px;
      border-radius: 5px;
    }
  }
}
</style>
<style lang="scss">
.addAddress {
  .van-picker__cancel,
  .van-picker__confirm {
    color: #1989fa !important;
  }
  .van-cell {
    padding: 5px 0;
  }
}
</style>
